"use client"

import { 
  ArrowDownToLine, 
  ArrowLeftRight, 
  ArrowUpRight, 
  ArrowDownRight,
  Wallet,
  Clock,
  ChevronRight,
  Plus,
  Shield,
  PiggyBank,
  Target,
  Loader2,
} from "lucide-react"
import Link from "next/link"
import { useState, useEffect } from "react"
import { useAuth } from "@/lib/auth-context"
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts"

const quickActions = [
  {
    title: "Novo deposito",
    description: "Pix ou Crypto",
    href: "/dashboard/depositos",
    icon: ArrowDownToLine,
    color: "emerald",
  },
  {
    title: "Nova transferencia",
    description: "Envie para outro usuario",
    href: "/dashboard/transferencias",
    icon: ArrowLeftRight,
    color: "blue",
  },
  {
    title: "Caixinhas",
    description: "Organize seu dinheiro",
    href: "/dashboard/caixinhas",
    icon: PiggyBank,
    color: "amber",
  },
  {
    title: "Metas",
    description: "Acompanhe seus objetivos",
    href: "/dashboard/metas",
    icon: Target,
    color: "purple",
  },
]

interface ChartTooltipProps {
  active?: boolean
  payload?: Array<{
    name: string
    value: number
    color: string
    dataKey: string
  }>
  label?: string
}

function CustomTooltip({ active, payload, label }: ChartTooltipProps) {
  if (active && payload && payload.length) {
    return (
      <div className="bg-zinc-900 border border-zinc-700 rounded-lg p-3 shadow-xl">
        <p className="text-zinc-400 text-xs mb-2">{label}</p>
        {payload.map((entry, index) => (
          <p key={index} className="text-sm" style={{ color: entry.color }}>
            {entry.dataKey === "entradas" ? "Entradas" : entry.dataKey === "saidas" ? "Saidas" : "Saldo"}: R$ {entry.value.toLocaleString("pt-BR")}
          </p>
        ))}
      </div>
    )
  }
  return null
}

export default function DashboardPage() {
  const { user } = useAuth()
  const [chartPeriod, setChartPeriod] = useState<"7" | "30" | "90">("7")
  const [isLoading, setIsLoading] = useState(true)
  const [stats, setStats] = useState({
    saldo: 0,
    emProcessamento: 0,
    depositosPendentes: 0,
    totalEntradas: 0,
    totalSaidas: 0,
  })
  const [chartData, setChartData] = useState<any[]>([])
  const [recentTransactions, setRecentTransactions] = useState<any[]>([])
  const [is2FAEnabled, setIs2FAEnabled] = useState(false)

  useEffect(() => {
    const fetchDashboardData = async () => {
      if (!user) return

      try {
        const accessToken = localStorage.getItem("frcabuloso_access_token")
        if (!accessToken) return

        // Fetch dashboard data from API
        const response = await fetch("/api/dashboard", {
          headers: {
            "Authorization": `Bearer ${accessToken}`,
          },
        })

        if (response.ok) {
          const data = await response.json()
          
          setStats({
            saldo: data.saldo ?? 0,
            emProcessamento: data.emProcessamento ?? 0,
            depositosPendentes: data.depositosPendentes ?? 0,
            totalEntradas: data.totalEntradas ?? 0,
            totalSaidas: data.totalSaidas ?? 0,
          })
          
          setChartData(data.chartData || [])
          setRecentTransactions(data.recentTransactions || [])
          setIs2FAEnabled(data.twoFactorEnabled || false)
        }
      } catch (error) {
        console.error("Error fetching dashboard data:", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchDashboardData()
  }, [user])

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <Loader2 className="w-8 h-8 text-emerald-500 animate-spin" />
      </div>
    )
  }

  const formattedStats = [
    {
      title: "Saldo disponivel",
      value: `R$ ${stats.saldo.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}`,
      icon: Wallet,
      color: "emerald",
    },
    {
      title: "Em processamento",
      value: `R$ ${stats.emProcessamento.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}`,
      subtitle: `${stats.depositosPendentes} depositos pendentes`,
      icon: Clock,
      color: "amber",
    },
    {
      title: "Total de entradas",
      value: `R$ ${stats.totalEntradas.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}`,
      icon: ArrowDownToLine,
      color: "blue",
    },
    {
      title: "Total de saidas",
      value: `R$ ${stats.totalSaidas.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}`,
      icon: ArrowLeftRight,
      color: "zinc",
    },
  ]

  return (
    <div className="space-y-4 sm:space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-xl sm:text-2xl font-semibold text-white">Dashboard</h1>
          <p className="text-zinc-500 text-xs sm:text-sm mt-1">Bem-vindo de volta, {user?.name || "Usuário"}</p>
        </div>
        <div className="flex items-center gap-2 sm:gap-3">
          <Link
            href="/dashboard/transferencias"
            className="flex items-center gap-2 px-3 sm:px-4 py-2 bg-zinc-800 hover:bg-zinc-700 text-white text-xs sm:text-sm font-medium rounded-lg transition-colors"
          >
            <ArrowLeftRight className="w-4 h-4" />
            <span className="hidden sm:inline">Transferir</span>
          </Link>
          <Link
            href="/dashboard/depositos"
            className="flex items-center gap-2 px-3 sm:px-4 py-2 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 text-xs sm:text-sm font-medium rounded-lg transition-colors"
          >
            <Plus className="w-4 h-4" />
            <span>Depositar</span>
          </Link>
        </div>
      </div>

      {/* Security Alert - if 2FA not enabled */}
      {!is2FAEnabled && (
        <div className="bg-amber-500/10 border border-amber-500/20 rounded-xl p-3 sm:p-4">
          <div className="flex items-start gap-3">
            <div className="w-8 h-8 rounded-lg bg-amber-500/20 flex items-center justify-center flex-shrink-0">
              <Shield className="w-4 h-4 text-amber-400" />
            </div>
            <div className="flex-1 min-w-0">
              <p className="text-white font-medium text-sm">Proteja sua conta</p>
              <p className="text-zinc-400 text-xs sm:text-sm mt-1">
                Ative a autenticacao 2FA para maior seguranca nas suas transacoes.
              </p>
            </div>
            <Link
              href="/dashboard/configuracoes"
              className="text-amber-400 hover:text-amber-300 text-xs sm:text-sm font-medium whitespace-nowrap"
            >
              Configurar
            </Link>
          </div>
        </div>
      )}

      {/* Stats Grid */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4">
        {formattedStats.map((stat) => (
          <div
            key={stat.title}
            className="p-3 sm:p-5 bg-zinc-900/50 border border-zinc-800 rounded-xl hover:border-zinc-700 transition-colors"
          >
            <div className="flex items-center justify-between mb-3 sm:mb-4">
              <div className={`w-8 h-8 sm:w-10 sm:h-10 rounded-lg flex items-center justify-center ${
                stat.color === "emerald" ? "bg-emerald-500/10" :
                stat.color === "amber" ? "bg-amber-500/10" :
                stat.color === "blue" ? "bg-blue-500/10" :
                "bg-zinc-800"
              }`}>
                <stat.icon className={`w-4 h-4 sm:w-5 sm:h-5 ${
                  stat.color === "emerald" ? "text-emerald-400" :
                  stat.color === "amber" ? "text-amber-400" :
                  stat.color === "blue" ? "text-blue-400" :
                  "text-zinc-400"
                }`} />
              </div>
            </div>
            <p className="text-zinc-500 text-xs sm:text-sm mb-1 truncate">{stat.title}</p>
            <p className="text-white text-lg sm:text-2xl font-semibold truncate">{stat.value}</p>
            {stat.subtitle && (
              <p className="text-zinc-600 text-xs mt-1 hidden sm:block">{stat.subtitle}</p>
            )}
          </div>
        ))}
      </div>

      {/* Quick Actions + Chart */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 sm:gap-6">
        {/* Quick Actions */}
        <div className="space-y-3 sm:space-y-4">
          <h2 className="text-white font-medium text-sm sm:text-base">Acoes rapidas</h2>
          <div className="grid grid-cols-2 gap-2 sm:gap-3">
            {quickActions.map((action) => (
              <Link
                key={action.title}
                href={action.href}
                className="flex flex-col items-center gap-1.5 sm:gap-2 p-3 sm:p-4 bg-zinc-900/50 border border-zinc-800 rounded-xl hover:border-zinc-700 transition-colors group text-center"
              >
                <div className={`w-8 h-8 sm:w-10 sm:h-10 rounded-lg flex items-center justify-center ${
                  action.color === "emerald" ? "bg-emerald-500/10" :
                  action.color === "blue" ? "bg-blue-500/10" :
                  action.color === "amber" ? "bg-amber-500/10" :
                  "bg-purple-500/10"
                }`}>
                  <action.icon className={`w-4 h-4 sm:w-5 sm:h-5 ${
                    action.color === "emerald" ? "text-emerald-400" :
                    action.color === "blue" ? "text-blue-400" :
                    action.color === "amber" ? "text-amber-400" :
                    "text-purple-400"
                  }`} />
                </div>
                <div>
                  <p className="text-white font-medium text-xs sm:text-sm">{action.title}</p>
                  <p className="text-zinc-500 text-[10px] sm:text-xs hidden sm:block">{action.description}</p>
                </div>
              </Link>
            ))}
          </div>
        </div>

        {/* Balance Chart */}
        <div className="lg:col-span-2 p-4 sm:p-6 bg-zinc-900/50 border border-zinc-800 rounded-xl">
          <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-3 mb-4 sm:mb-6">
            <h2 className="text-white font-medium text-sm sm:text-base">Evolucao do saldo</h2>
            <select 
              value={chartPeriod}
              onChange={(e) => setChartPeriod(e.target.value as "7" | "30" | "90")}
              className="px-3 py-1.5 bg-zinc-800 border border-zinc-700 rounded-lg text-zinc-400 text-xs sm:text-sm focus:outline-none focus:border-emerald-500 cursor-pointer w-full sm:w-auto"
            >
              <option value="7">Ultimos 7 dias</option>
              <option value="30">Ultimos 30 dias</option>
              <option value="90">Ultimos 90 dias</option>
            </select>
          </div>
          
          {/* Recharts Area Chart */}
          <div className="h-48 sm:h-56 -ml-2 sm:ml-0">
            {chartData.length > 0 ? (
              <ResponsiveContainer width="100%" height="100%">
                <AreaChart
                  data={chartData}
                  margin={{ top: 5, right: 5, left: -20, bottom: 5 }}
                >
                <defs>
                  <linearGradient id="colorEntradas" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor="#10b981" stopOpacity={0.3} />
                    <stop offset="95%" stopColor="#10b981" stopOpacity={0} />
                  </linearGradient>
                  <linearGradient id="colorSaidas" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor="#ef4444" stopOpacity={0.3} />
                    <stop offset="95%" stopColor="#ef4444" stopOpacity={0} />
                  </linearGradient>
                </defs>
                <CartesianGrid strokeDasharray="3 3" stroke="#27272a" vertical={false} />
                <XAxis 
                  dataKey="name" 
                  axisLine={false}
                  tickLine={false}
                  tick={{ fill: "#71717a", fontSize: 11 }}
                  dy={10}
                />
                <YAxis 
                  axisLine={false}
                  tickLine={false}
                  tick={{ fill: "#71717a", fontSize: 11 }}
                  tickFormatter={(value) => `${(value / 1000).toFixed(0)}k`}
                  width={35}
                />
                <Tooltip content={<CustomTooltip />} />
                <Area
                  type="monotone"
                  dataKey="entradas"
                  stroke="#10b981"
                  strokeWidth={2}
                  fillOpacity={1}
                  fill="url(#colorEntradas)"
                />
                <Area
                  type="monotone"
                  dataKey="saidas"
                  stroke="#ef4444"
                  strokeWidth={2}
                  fillOpacity={1}
                  fill="url(#colorSaidas)"
                />
              </AreaChart>
            </ResponsiveContainer>
            ) : (
              <div className="flex items-center justify-center h-full text-zinc-500 text-sm">
                Sem dados para exibir
              </div>
            )}
          </div>

          <div className="flex items-center justify-center gap-4 sm:gap-6 mt-4 sm:mt-6 pt-4 sm:pt-6 border-t border-zinc-800">
            <div className="flex items-center gap-2">
              <div className="w-2.5 h-2.5 sm:w-3 sm:h-3 rounded-full bg-emerald-500" />
              <span className="text-zinc-500 text-xs sm:text-sm">Entradas</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-2.5 h-2.5 sm:w-3 sm:h-3 rounded-full bg-red-500" />
              <span className="text-zinc-500 text-xs sm:text-sm">Saidas</span>
            </div>
          </div>
        </div>
      </div>

      {/* Recent Transactions */}
      <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl">
        <div className="flex items-center justify-between p-3 sm:p-4 border-b border-zinc-800">
          <h2 className="text-white font-medium text-sm sm:text-base">Transacoes recentes</h2>
          <Link
            href="/dashboard/carteira"
            className="text-emerald-400 text-xs sm:text-sm hover:text-emerald-300 flex items-center gap-1"
          >
            Ver todas
            <ChevronRight className="w-4 h-4" />
          </Link>
        </div>
        
        <div className="divide-y divide-zinc-800">
          {recentTransactions.length > 0 ? (
            recentTransactions.map((transaction) => (
            <div
              key={transaction.id}
              className="flex items-center justify-between p-3 sm:p-4 hover:bg-zinc-800/30 transition-colors"
            >
              <div className="flex items-center gap-3 sm:gap-4 min-w-0">
                <div className={`w-8 h-8 sm:w-10 sm:h-10 rounded-lg flex items-center justify-center flex-shrink-0 ${
                  transaction.type === "deposit" ? "bg-emerald-500/10" :
                  transaction.type === "transfer_in" ? "bg-blue-500/10" :
                  "bg-zinc-800"
                }`}>
                  {transaction.type === "deposit" ? (
                    <ArrowDownToLine className="w-4 h-4 sm:w-5 sm:h-5 text-emerald-400" />
                  ) : transaction.type === "transfer_in" ? (
                    <ArrowDownRight className="w-4 h-4 sm:w-5 sm:h-5 text-blue-400" />
                  ) : (
                    <ArrowUpRight className="w-4 h-4 sm:w-5 sm:h-5 text-zinc-400" />
                  )}
                </div>
                <div className="min-w-0">
                  <p className="text-white text-xs sm:text-sm font-medium truncate">{transaction.description}</p>
                  <p className="text-zinc-500 text-[10px] sm:text-xs">{transaction.date}</p>
                </div>
              </div>
              <div className="text-right flex-shrink-0 ml-2">
                <p className={`text-xs sm:text-sm font-medium ${
                  transaction.amount.startsWith("+") ? "text-emerald-400" : "text-zinc-400"
                }`}>
                  {transaction.amount}
                </p>
                <p className="text-zinc-600 text-[10px] sm:text-xs hidden sm:block">Taxa: {transaction.fee}</p>
                <p className={`text-[10px] sm:text-xs mt-0.5 ${
                  transaction.status === "completed" ? "text-zinc-500" : "text-amber-400"
                }`}>
                  {transaction.status === "completed" ? "Concluido" : "Pendente"}
                </p>
              </div>
            </div>
            ))
          ) : (
            <div className="p-8 text-center">
              <Wallet className="w-12 h-12 text-zinc-700 mx-auto mb-3" />
              <p className="text-zinc-500 text-sm">Nenhuma transação encontrada</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
